import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { DefaultObjDTO } from 'src/app/models/default-obj.dto';

@Injectable({
  providedIn: 'root'
})
export class PaginacaoService {

  private promisse: any;

  private use: boolean = false;
  private idJson: any = '';
  private nomeJson: any = '';
  private descricaoJson: any = '';
  private tipoJson: any = '';
  private imgURLJson: any = '';
  private objs: DefaultObjDTO[] = [];
  private emptyMessage: string;
  private add: boolean = false;
  private paginaAtual: number = 0;
  closeBTN: boolean = false;
  isModal: boolean = false;

  private internalLoad = false;
  private functionPromissed;
  private currentTab: string;

  constructor() { }

  setConfiguration(use: boolean, idJson?: any, nomeJson?: any, descricaoJson?: any, tipoJson?: any,
    imgURLJson?: any, objs?: DefaultObjDTO[], emptyMessage?: string, add?: boolean, paginaAtual?: number, closeBTN?: boolean, isModal?: boolean) {
    this.use = use;
    this.idJson = idJson;
    this.nomeJson = nomeJson;
    this.descricaoJson = descricaoJson;
    this.tipoJson = tipoJson;
    this.imgURLJson = imgURLJson;
    this.objs = objs;
    this.emptyMessage = emptyMessage;
    this.add = add;
    this.paginaAtual = paginaAtual;
    this.closeBTN = closeBTN;
    this.isModal = isModal;
  }

  getConfiguration() {
    return {
      use: this.use,
      idJson: this.idJson,
      nomeJson: this.nomeJson,
      descricaoJson: this.descricaoJson,
      tipoJson: this.tipoJson,
      imgURLJson: this.imgURLJson,
      objs: this.objs,
      emptyMessage: this.emptyMessage,
      add: this.add,
      paginaAtual: this.paginaAtual,
      closeBTN: this.closeBTN,
      isModal: this.isModal
    }
  }

  setPromisse(p) {
    this.promisse = p;
  }

  getPromisse() {
    return this.promisse;
  }

  setFunctionPromissed(f) {
    this.functionPromissed = f;
  }

  getFunctionPromissed() {
    return this.functionPromissed;
  }

  private subject = new Subject<any>();

  requestRefresh(doRefresh: boolean) {
    this.subject.next(doRefresh);
  }

  getRefresh(): Observable<any> {
    return this.subject.asObservable();
  }

  sendNewPageRequest(value: number) {
    if (!value)
      value = 0;
    this.subject.next(value);
  }

  getNewPageRequest(): Observable<any> {
    return this.subject.asObservable();
  }


  getInternalLoad() {
    return this.internalLoad
  }

  setInternalLoad(value: boolean) {
    this.internalLoad = value;
  }

  setPaginaAtual(value: number) {
    this.paginaAtual = value;
  }

  getCurrentTab() {
    return this.currentTab;
  }

  setCurrentTab(value: string) {
    this.currentTab = value;
  }

}
